    "Cest un rsultat passable. Essayez de vous impliquer plus, et de mieux vous insrer dans lcosystme de la route. Rvisez votre connaissance du Code. Cherchez  mieux comprendre ce qui se passe sur la Route. Pensez aux autres pour amliorer votre comportement. Et pourquoi ne pas sintresser aux nouvelles mobilits ?",
    "Pas mal ! Le Code  encore quelques secrets pour vous, mais vous avez dj une relativement bonne comprhension des spcificits et des dangers de la Route. Votre attitude est positive, vous reste a devenir encore plus attentif aux autres et respectueux des rgles. La culture de la route ne vous mobilise pas, mais vous avez une bonne mmoire. Et si vous essayiez de vous amliorer ?",
    "Bravo ! Vous tes au dessus de la moyenne en ce qui concerne votre connaissance du Code. Vous avez une bonne comprhension du fonctionnement de la Route. A cela sajoute un comportement responsable, et une culture des mobilits assez pointue. Vous avez tout pour tre un bon conducteur. Pourquoi ne pas viser la perfection ?",
    "Sidrant. connaissance parfaite du Code, excellente comprhension de lcosystme de la Route dans toutes ses composantes, et comportement exemplaire ! De surcrot, rien dans la culture de la Route ne vous chappe. Nous navons (presque) plus rien  vous apprendre. Vous ne voulez pas devenir Moniteur ?"
